.page {
  min-width: 320px;
  height: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  background-color: #ffffff;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}
.logo {
  cursor: pointer;
}
.logo:hover svg {
  fill: #D22856;
}
.logo:focus-visible {
