}
.logo--footer {
  width: 145px;
  height: 40px;
}
@media (min-width: 1200px) {
  .logo--footer {
    width: 147px;
    height: 40px;
  }
}
.logo--footer:active path {
  fill: rgba(0, 0, 0, 0.3);
}
@media (min-width: 660px) {
  .logo--header .logo__mobile {
    display: none;
  }
}
.logo--header .logo__tablet {
  display: none;
}
@media (min-width: 660px) {
  .logo--header .logo__tablet {
    display: block;
  }
}
@media (min-width: 1200px) {
  .logo--header .logo__tablet {
    display: none;
  }
}
.logo--header .logo__desktop {
  display: none;
}
@media (min-width: 1200px) {
  .logo--header .logo__desktop {
    display: block;
  }
}
.logo--footer .logo__tablet {
  display: block;
}
@media (min-width: 1200px) {
  .logo--footer .logo__tablet {
    display: none;
  }
}
.logo--footer .logo__desktop {
  display: none;
}
@media (min-width: 1200px) {
  .logo--footer .logo__desktop {
    display: block;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.slider__toggles {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.slider__toggle {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  position: relative;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.slider__toggle::before {
  position: absolute;
  width: 6px;
  height: 6px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  content: "";
  background-color: rgba(40, 54, 69, 0.3);
  border-radius: 50%;
}
.slider__toggle:hover::before {
  opacity: 0.6;
}
.slider__toggle:focus-visible::before {
  background-color: #283645;
}
.slider__toggle:focus {
  outline: none;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__toggle--current {
  border: 3px solid #283645;
}
.slider__toggle--current::before {
  background-color: transparent;
}
.slider__toggle--current:hover {
  background-color: #283645;
}
.slider__toggle--current:active {
  background-color: #283645;
}
.slider__control {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  width: 22px;
  height: 41px;
  background-image: url("../../img/arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.3;
}
.slider__control--next {
  transform: rotate(180deg);
}
.slider__control:hover {
  opacity: 0.6;
}
.slider__control:focus-visible {
  outline: 2px solid #283645;
  outline-offset: 2px;
}
.slider__control:active {
  opacity: 0.1;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
