  box-sizing: border-box;
  width: 100%;
  padding: 14.5px 20px;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.5px;
}
.contest__field--textarea {
  min-height: 201px;
  padding: 12.5px 20px;
  letter-spacing: normal;
  font-family: inherit;
  color: #283645;
  resize: none;
